import ActionForm from './action-form.js'

const Action = function () {
  const state = {
    emitter: null
  }

  return {
    init
  }

  /**
   * üîë Public Methods
   */
  function init ({ alias, emitter }) {
    const mountComponents = [
      'report',
      'novoResponsavel',
      'avaliacao'
    ].includes(alias)

    state.emitter = emitter
    state.alias = alias

    if (mountComponents) {
      mount()
    }
  }

  /**
   * üîí Private Methods
   */
  function mount () {
    const { emitter, alias } = state

    ActionForm.mount({ emitter, alias })
  }
}

export default Action()
