import Drawer from '../../components/drawer'
import ActionsService from '../api/api.actions'

const DrawerRemote = function () {
  const $refs = {
    drawer: document.querySelector('#actions-drawer'),
    table: document.querySelector('#actions-table'),
    token: document.querySelector('#inpToken'),
    codFlowExecute: document.querySelector('#inpCodFlowExecute'),
    risksFields: [
      document.querySelector('[xname=inpplanoResponsavel]'),
      document.querySelector('[xname=inpcodplanoResponsavel]'),
      document.querySelector('[xname=inpplanoOrigem]'),
      document.querySelector('[xname=inptipoPlano]'),
      document.querySelector('[xname=inpperiodoInicio]'),
      document.querySelector('[xname=inpdepartamento]'),
      document.querySelector('[xname=inpgerencia]'),
      document.querySelector('[xname=inpunidade]'),
      ...document.querySelectorAll('[xname^=inprisco]')
    ]
  }

  const state = {
    emitter: null,
    token: $refs.token.value,
    codFlowExecute: $refs.codFlowExecute.value
  }

  return {
    mount
  }

  /**
   * üîë Public Methods
   */
  function mount ({ emitter }) {
    state.emitter = emitter
    state.drawer = Drawer($refs.drawer, {
      onConfirm,
      onConclude,
      fieldRef: 'data-name',
      title: 'Adicionar A√ß√£o',
      confirmText: 'Adicionar'
    })
  }

  /**
   * üîí Private Methods
   */
  function onConfirm (row, data) {
    return [
      ActionsService.createAction({
        formValues: {
          ...data,
          ...getRisksData()
        },
        token: state.token,
        codFlowExecute: state.codFlowExecute
      }),
      ActionsService.updateActionsList({
        formValues: {
          table: [...getActionsData(), data]
        },
        token: state.token,
        codFlowExecute: state.codFlowExecute
      })
    ]
  }

  function onConclude (res, data) {
    state.emitter.emit('list-add', data)
  }

  function getActionsData () {
    return [
      ...$refs.table.querySelectorAll('tr:not(.header)')
    ]
      .map(row =>
        [...row.querySelectorAll('[xname]')]
          .reduce(formatFieldToObject, {})
      )
  }

  function getRisksData () {
    return $refs.risksFields
      .reduce(formatFieldToObject, {})
  }

  function formatFieldToObject (fields, field) {
    const id = field.getAttribute('xname').substring(3)
    const value = field.value

    fields[id] = value
    return fields
  }
}

export default DrawerRemote()
