import ComponentError from '../helpers/component-error'

const defaults = {
  hiddenClass: 'u-hidden'
}

export default function FieldToggler (fieldId, params) {
  params = {
    ...defaults,
    ...params,
    name: 'FieldToggler'
  }

  const field = getOrquestraField(fieldId, params)

  const $refs = {}

  const instance = {
    field
  }

  mount()

  instance.field._fieldToggler = instance

  return instance

  /**
   * üîí Private Methods
   */
  function mount () {
    mountTogglerField(params.disabled)
    mountTogglerLabel(params.disabled)

    if (!params.disabled) {
      addTriggers()
    }
  }

  function addTriggers () {
    instance.editBtn.addEventListener('click', showField)
    instance.confirmBtn.addEventListener('click', showLabel)
    instance.field.addEventListener('keyup', handleEmptyValue)

    $(instance.field).on('change', handleEmptyValue)
  }

  function showField (e) {
    e.preventDefault()

    $refs.togglerField.classList.remove(params.hiddenClass)
    $refs.togglerLabel.classList.add(params.hiddenClass)
  }

  function showLabel (e) {
    e.preventDefault()

    $refs.togglerField.classList.add(params.hiddenClass)
    $refs.togglerLabel.classList.remove(params.hiddenClass)

    updateLabel()
  }

  function updateLabel () {
    $refs.togglerTemplate.remove()

    $refs.togglerLabel.insertAdjacentHTML(
      'afterbegin', params.template(instance.field.value)
    )

    $refs.togglerTemplate = $refs.togglerLabel.firstElementChild
  }

  function handleEmptyValue () {
    const isEmpty = instance.field.value === ''
    instance.confirmBtn.disabled = isEmpty
  }

  function mountTogglerField () {
    const container = instance.field.parentElement

    container.insertAdjacentHTML(
      'beforeend',
      `<div class="field-toggler ${params.hiddenClass}" data-toggler-field></div>`
    )

    $refs.togglerField = container
      .querySelector('.field-toggler[data-toggler-field]')

    $refs.togglerField
      .appendChild(instance.field)

    if (!params.disabled) {
      const confirmBtn = getConfirmBtn()

      $refs.togglerField.insertAdjacentHTML(
        'beforeend', confirmBtn
      )

      instance.confirmBtn = $refs.togglerField
        .querySelector('.field-toggler-action')
    } else {
      document.querySelector(`div[xid=div${fieldId}]`).remove()
    }
  }

  function mountTogglerLabel () {
    $refs.togglerField.insertAdjacentHTML(
      'afterend',
      '<div class="field-toggler" data-toggler-label></div>'
    )

    $refs.togglerLabel = $refs.togglerField.nextSibling

    $refs.togglerLabel.insertAdjacentHTML(
      'afterbegin', params.template(instance.field.value)
    )

    $refs.togglerTemplate = $refs.togglerLabel.firstElementChild

    if (!params.disabled) {
      const editBtn = getEditBtn()

      $refs.togglerLabel.insertAdjacentHTML(
        'beforeend', editBtn
      )

      instance.editBtn = $refs.togglerLabel
        .querySelector('.field-toggler-action')
    }
  }

  function getConfirmBtn () {
    return '<button class="field-toggler-action btn btn-link"><i class="btn-icon fas fa-check"></i> Salvar</button>'
  }

  function getEditBtn () {
    return '<button class="btn btn-link field-toggler-action"><i class="btn-icon fas fa-pen"></i> Alterar</button>'
  }

  /**
   * üîë Public Methods
   * - open
   * - close
   * - update (update label)
   */
}

/**
 * üí° Helper Functions
 */
function getOrquestraField (fieldId, params) {
  const field = document.querySelector(`[xname=inp${fieldId}]`)

  if (!field) {
    throw new ComponentError({
      name: params.name,
      message:
      `Erro ao criar a inst√¢ncia do componente.\nNenhum campo do Orquestra encontrado para a refer√™ncia ${fieldId}`,
      type: 'instace_field_not_found'
    })
  }

  return field
}
