const addDays = (date, quantity) => {
  const newDate = new Date(date)
  newDate.setDate(date.getDate() + parseInt(quantity))

  return newDate
}

const formatDate = date =>
  (date).toLocaleDateString('pt-BR')

/**
 * üîë Public Helpers
 */
export function setAlertDate (alertInterval, alertDate) {
  if (alertInterval.value === '') {
    return
  }

  const date = addDays(new Date(), alertInterval.value)
  alertDate.value = formatDate(date)
}
