import Form from '../../components/form'
import { getStatusTag } from '../action-list/list-card'
import { setAlertDate } from '../action-plan/actions-helpers'
import FieldToggler from '../../components/field-toggler'

const fieldsTemplates = {
  assignee (value) {
    return `<div><i class="form-text-icon fas fa-user"></i> ${value}</div>`
  },
  duedate (value) {
    return `<div><i class="form-text-icon fas fa-calendar"></i> ${value}</div>`
  },
  alertInterval (value) {
    return `<div><i class="form-text-icon fas fa-bell"></i> ${value} dias</div>`
  },
  status: getStatusTag
}

const ActionForm = function () {
  const $refs = {
    table: document.querySelector('#actions-table'),
    tableFields: {
      alertDate: document.querySelector('[xname=inpacaoMonitoramentoPrazo]'),
      duedate: document.querySelector('[xname=inpacaoTermino]')
    },
    formFields: {
      assignee: document.querySelector('[xname=inpacaoResponsavel_form]'),
      assigneeCodUser: document.querySelector('[xname=inpcodacaoResponsavel_form]'),
      alertInterval: document.querySelector('[xname=inpacaoMonitoramento_form]'),
      duedate: document.querySelector('[xname=inpacaoPrazo_form]'),
      status: document.querySelector('[xname=inpacaoStatus_form]'),
      finishedAt: document.querySelector('[xname=inpacaoTermino_form]')
    }
  }

  const state = {
    finishedAtId: 'acaoTermino_form',
    emitter: null
  }

  return {
    mount
  }

  /**
   * üîë Public Methods
   */
  function mount ({ emitter, alias }) {
    state.emitter = emitter
    state.alias = alias

    mountTable()
    addTriggers()
    toggleFinishedAt()
    handleFormTogglers()
  }

  /**
   * üîí Private Methods
   */
  function addTriggers () {
    $refs.formFields.status
      .addEventListener('change', toggleFinishedAt)

    if (state.alias === 'avaliacao') {
      $refs.formFields.alertInterval
        .addEventListener('change', e =>
          setAlertDate(e.target, $refs.tableFields.alertDate)
        )
    }
  }

  function mountTable () {
    $refs.tableFields.duedate
      .setAttribute('required', 'N')

    Object.values($refs.formFields)
      .forEach(field => {
        updateTableValue(field)
        $(field).on('change', () => updateTableValue(field))
      })

    if (state.alias === 'avaliacao') {
      setAlertDate(
        $refs.formFields.alertInterval,
        $refs.tableFields.alertDate
      )
    }
  }

  function handleFormTogglers () {
    const { alias } = state

    if (alias === 'report' || alias === 'avaliacao') {
      FieldToggler('acaoStatus_form', {
        template: fieldsTemplates.status
      })
    }

    if (alias === 'report') {
      FieldToggler('acaoResponsavel_form', {
        template: fieldsTemplates.assignee,
        disabled: true
      })
      FieldToggler('acaoPrazo_form', {
        template: fieldsTemplates.duedate,
        disabled: true
      })
      FieldToggler('acaoMonitoramento_form', {
        template: fieldsTemplates.alertInterval,
        disabled: true
      })
    }

    if (alias === 'novoResponsavel') {
      FieldToggler('acaoPrazo', {
        template: fieldsTemplates.duedate,
        disabled: true
      })
      FieldToggler('acaoMonitoramento_form', {
        template: fieldsTemplates.alertInterval,
        disabled: true
      })
      FieldToggler('acaoStatus_form', {
        template: fieldsTemplates.status,
        disabled: true
      })
    }

    if (alias === 'avaliacao') {
      FieldToggler('acaoResponsavel_form', {
        template: fieldsTemplates.assignee
      })
      FieldToggler('acaoPrazo_form', {
        template: fieldsTemplates.duedate
      })
      FieldToggler('acaoMonitoramento_form', {
        template: fieldsTemplates.alertInterval
      })
    }
  }

  function toggleFinishedAt () {
    const status = $refs.formFields.status.value

    if (status === 'Conclu√≠da' || status === 'Cancelada') {
      Form.showField(state.finishedAtId)
    } else {
      Form.hideField(state.finishedAtId)
    }
  }

  function updateTableValue (field) {
    const id = field.getAttribute('xname').split('_')[0]
    const tableField = $refs.table.querySelector(`[xname=${id}]`)

    tableField.value = field.value
  }
}

export default ActionForm()
