/**
 * Dependencies
 */
import mitt from 'mitt'

/**
 * Global Components
 */
import Form from './components/form'
import ButtonRadio from './components/button-radio'

/**
 * Modules
 */
import ActionPlan from './modules/action-plan'
import ActionList from './modules/action-list'
import ActionDrawer from './modules/action-drawer'
import Action from './modules/action'

const emitter = mitt()
const alias = document.querySelector('#inpDsFlowElementAlias').value

Form.conditionals()
ButtonRadio('[data-btn-radio]', { hidePrefix: true })

/**
 * Initialize Modules
 */
ActionPlan.init({ alias, emitter })
ActionList.init({ alias, emitter })
ActionDrawer.init({ alias, emitter })
Action.init({ alias, emitter })
