import Form from '../../components/form'

const ActionsForm = function () {
  const $refs = {
    section: document.querySelector('#section-actions'),
    isAssignee: document.querySelector('[xname=inpresponsavelPeloPlano]'),
    status: document.querySelector('[xname=inpplanoStatus]'),
    assignee: document.querySelector('[xname=inpplanoResponsavel]'),
    codUser: document.querySelector('[xname=inpcodplanoResponsavel]'),
    currentAssignee: document.querySelector('[xname=inptempResponsavelPlano]'),
    currentCodUser: document.querySelector('[xname=inpcodtempResponsavelPlano]')
  }

  const state = {
    tableRef: '#actions-table',
    emitter: null
  }

  return {
    mount,
    showActionsList,
    removeOptionsFromStatus
  }

  /**
   * üîë Public Methods
   */
  function mount ({ emitter }) {
    state.emitter = emitter

    $refs.assignee.closest('.form-group')
      .classList.add('u-hidden')

    $refs.isAssignee
      .addEventListener('change', toggleActions)

    toggleActions()
  }

  function showActionsList () {
    $refs.section
      .classList.remove('u-hidden')
  }

  function removeOptionsFromStatus (options) {
    const $options = $refs.status.querySelectorAll('option')

    $options.forEach(option => {
      if (options.includes(option.value)) {
        option.remove()
      }
    })
  }

  /**
   * üîí Private Methods
   */
  function toggleActions () {
    const isAssignee = $refs.isAssignee.value

    if (isAssignee === '1. Sim') {
      Form.showTable(state.tableRef)
      Form.hideField('planoResponsavel')

      $refs.assignee.value = $refs.currentAssignee.value
      $refs.codUser.value = $refs.currentCodUser.value

      return showActionsList()
    }

    if (isAssignee === '2. N√£o') {
      $refs.assignee.value = ''

      Form.hideTable(state.tableRef)
      Form.showField('planoResponsavel')

      state.emitter.emit('list-clear')

      $refs.section
        .classList.add('u-hidden')
    }
  }
}

export default ActionsForm()
