import VirtualTable from '../../components/virtual-table'
import CreateCard from './list-card'

const ListUtil = function () {
  const $refs = {
    table: document.querySelector('#actions-table'),
    vtable: document.querySelector('#actions-list'),
    emptyList: document.querySelector('#actions-list-empty'),
    action: document.querySelector('#actions-list-action')
  }

  const state = {
    emitter: null
  }

  return {
    init,
    mount,
    append,
    clear
  }

  /**
   * üîë Public Methods
   */
  function init ({ emitter }) {
    state.emitter = emitter

    state.vTable = VirtualTable({
      table: $refs.table,
      vtable: $refs.vtable,
      empty: $refs.emptyList,
      action: $refs.action,
      template: CreateCard,
      onEdit: onEditCard
    })
  }

  function mount () {
    state.vTable.mount()
  }

  function append (data) {
    state.vTable.append(data)
  }

  function clear () {
    state.vTable.clear()
  }

  /**
   * üîí Private Methods
   */
  function onEditCard (index, data, vRow) {
    state.emitter.emit('card-edit', {
      data,
      ref: vRow,
      onRemove: onRemoveCard,
      onConfirm: onSaveCard
    })
  }

  function onRemoveCard (row) {
    state.vTable.remove(row)
  }

  function onSaveCard (row, data) {
    state.vTable.replace(row, {
      ...data,
      editable: true,
      removeable: true
    })
  }
}

export default ListUtil()
