import ComponentError from '../helpers/component-error'
import resolveReference from '../helpers/component-reference'
import { removeOptionPrefix } from '../helpers/component-helpers'

const defaults = {
  groupClass: 'btn-radio-group',
  btnClass: 'btn-radio',
  activeClass: '-active',
  hidePrefix: false,
  splitSeparator: '.'
}

export default function ButtonRadio (reference, params) {
  params = {
    ...defaults,
    ...params,
    name: 'ButtonRadio'
  }

  const elements = resolveReference(reference, params.name)

  if (elements instanceof HTMLElement) {
    const [element] = elements
    return createInstance(element, params)
  }

  return elements
    .map(element => createInstance(element, params))
    .filter(element => element !== undefined)
}

function createInstance (element, params) {
  const changeEvent = new Event('change')
  const {
    select,
    textvalue
  } = getOrquestraField(element, params)

  if (textvalue && params.hidePrefix) {
    removeTextPrefix(textvalue)
  }

  if (!select) {
    return
  }

  const { buttons, buttonGroup } = createButtonGroup(select, params)

  const state = {
    value: '',
    isEnable: true
  }

  const instance = {
    state,
    element,
    select,
    buttonGroup,
    buttons: [...buttons],
    reset,
    set,
    get
  }

  addListners(buttons, select)

  if (select.value !== '') {
    set(select.value, true)
  }

  element._buttonGroup = instance

  return instance

  /**
   * üîí Private Methods
   */
  function addListners (buttons, select) {
    buttons
      .forEach(button => {
        button.addEventListener('click', onClick)
      })

    select.addEventListener('change', onChange)
  }

  function onChange () {
    const value = this.value

    if (value === '') {
      reset()
    } else {
      set(value, true)
    }
  }

  function onClick (e, silent) {
    const option = this.dataset.option

    reset()

    this.classList.add(params.activeClass)
    instance.select.value = option
    state.value = option

    if (!silent) {
      instance.select.dispatchEvent(changeEvent)
    }
  }

  function removeTextPrefix (textvalue) {
    const value = removeOptionPrefix(textvalue.textContent, params.splitSeparator)
    textvalue.textContent = value
  }

  /**
   * üîë Public Methods
   * @todo Destroy / ShowOption / HideOption / Disable (readonly) / Enable / TextOnly
   */
  function reset () {
    state.value = ''
    instance.select.value = ''
    instance.buttons
      .forEach(button => {
        button.classList.remove(params.activeClass)
      })
  }

  function set (option, silent = false) {
    const button = instance.buttons
      .find(button => button.dataset.option === option)

    if (!button) {
      throw new ComponentError({
        name: params.name,
        message:
        `Erro ao realizar set para novo valor.\nN√£o foi encontrada nenhuma op√ß√£o para o valor ${option}`,
        type: 'option_not_found'
      })
    }

    onClick.call(button, null, silent)
  }

  function get () {
    return state.value
  }
}

/**
 * üí° Helper Functions
 */

function getOrquestraField (element, params) {
  const select = element.querySelector('select[xname]')
  const textvalue = element.querySelector('div[xid]')
  const inputHidden = element.querySelector('input[type=hidden][xname]')

  if (!select && !textvalue && !inputHidden) {
    throw new ComponentError({
      name: params.name,
      message:
      'Erro ao criar a inst√¢ncia do componente.\nNenhum campo do Orquestra encontrado para a refer√™ncia informada',
      type: 'instace_field_not_found'
    })
  }

  return {
    select,
    textvalue
  }
}

function createButtonGroup (select, params) {
  const options = getOrquestraFieldOptions(select, params)
  const btnGroupHtml = getButtonGroupMarkup(options, params)

  select.insertAdjacentHTML('afterend', btnGroupHtml)
  select.style.display = 'none'

  const buttonGroup = select.nextSibling
  const buttons = buttonGroup.querySelectorAll(`.${params.btnClass}`)

  return {
    buttonGroup,
    buttons
  }
}

function getOrquestraFieldOptions (select, params) {
  return [...select.options]
    .filter(option => option.value)
    .map(option => {
      const value = option.value
      const label = params.hidePrefix
        ? removeOptionPrefix(value, params.splitSeparator)
        : value

      return { value, label }
    })
}

function getButtonGroupMarkup (options, { groupClass, btnClass }) {
  const buttons = options
    .map(option => getButtonMarkup(option, btnClass))

  return `<div class="btn-group ${groupClass}">${buttons.join('')}</div>`
}

function getButtonMarkup ({ value, label }, btnClass) {
  return `<button type="button" class="btn ${btnClass}" data-option="${value}">${label}</button>`
}
