import ActionsRisks from './actions-risks'
import ActionsForm from './actions-form'

const ActionPlan = function () {
  const $refs = {
    origin: document.querySelector('[xname=inpplanoOrigem]')
  }

  const state = {
    emitter: null,
    statusRemovedOptions: [
      'Cancelada',
      'Conclu√≠da'
    ]
  }

  return {
    init
  }

  /**
   * üîë Public Methods
   */
  function init ({ alias, emitter }) {
    const mountComponents = [
      'criacao',
      'acoes',
      'monitoramento',
      'novoResponsavelPlano',
      'report',
      'avaliacao',
      'novoResponsavel'
    ].includes(alias)

    state.emitter = emitter
    state.alias = alias
    state.isOriginFromRisks = $refs.origin.value === 'riscos'

    if (mountComponents) {
      mount()
    }
  }

  /**
   * üîí Private Methods
   */
  function mount () {
    const { emitter } = state

    if (state.isOriginFromRisks) {
      ActionsRisks.mount()
    }

    if (state.alias === 'criacao') {
      ActionsForm.mount({ emitter })
    }

    if ([
      'acoes',
      'monitoramento'
    ].includes(state.alias)) {
      ActionsForm.showActionsList()
    }

    ActionsForm
      .removeOptionsFromStatus(state.statusRemovedOptions)
  }
}

export default ActionPlan()
