import Drawer from '../../components/drawer'

const DrawerStatic = function () {
  const $refs = {
    drawer: document.querySelector('#actions-drawer')
  }

  const state = {
    emitter: null
  }

  return {
    mount,
    open
  }

  /**
   * üîë Public Methods
   */
  function mount ({ emitter }) {
    state.emitter = emitter

    state.drawer = Drawer($refs.drawer, {
      onConfirm,
      fieldRef: 'data-name',
      title: 'Adicionar A√ß√£o',
      confirmText: 'Adicionar'
    })
  }

  function open (options) {
    const { data, ...props } = options

    if (data) {
      state.drawer.populate(data)
    }

    state.drawer.open({
      title: 'Editar A√ß√£o',
      confirmText: 'Salvar',
      enableRemove: true,
      ...props
    })
  }

  /**
   * üîí Private Methods
   */
  function onConfirm (row, data) {
    state.emitter.emit('list-add', {
      ...data,
      editable: true,
      removeable: true
    })
  }
}

export default DrawerStatic()
