import DrawerForm from './drawer-form'
import DrawerStatic from './drawer-static'
import DrawerRemote from './drawer-remote'

const ActionDrawer = function () {
  const state = {
    emitter: null
  }

  return {
    init
  }

  /**
   * üîë Public Methods
   */
  function init ({ alias, emitter }) {
    const mountComponents = [
      'criacao',
      'acoes',
      'monitoramento'
    ].includes(alias)

    state.emitter = emitter
    state.alias = alias

    state.emitter.on('card-edit', DrawerStatic.open)

    if (mountComponents) {
      mount()
    }
  }

  /**
   * üîí Private Methods
   */
  function mount () {
    const { emitter } = state

    DrawerForm.mount()

    if (
      state.alias === 'criacao' ||
      state.alias === 'acoes'
    ) {
      DrawerStatic.mount({ emitter })
    }

    if (state.alias === 'monitoramento') {
      DrawerRemote.mount({ emitter })
    }
  }
}

export default ActionDrawer()
