import { setAlertDate } from '../action-plan/actions-helpers'

const DrawerForm = function () {
  const $refs = {
    drawer: document.querySelector('#actions-drawer'),
    duedate: document.querySelector('[data-name=acaoPrazo]'),
    alertInterval: document.querySelector('[data-name=acaoMonitoramento]'),
    alertDate: document.querySelector('[data-name=acaoMonitoramentoPrazo]'),
    assignee: document.querySelector('[xname=inptempResponsavel]'),
    assigneeCodUser: document.querySelector('[xname=inpcodtempResponsavel]')
  }

  return {
    mount
  }

  /**
   * üîë Public Methods
   */
  function mount () {
    $refs.alertInterval
      .addEventListener('change', e =>
        setAlertDate(e.target, $refs.alertDate)
      )

    // TODO: criar component "s√≥ n√∫meros" para campo
    // monitoramento

    // TODO: adicionar tooltip quando bot√£o adicionar
    // estiver com estado disabled

    setAssigneeRefs()
    mountDatePicker()
    mountFormValidation()
  }

  /**
   * üîí Private Methods
   */
  function setAssigneeRefs () {
    $refs.assignee.setAttribute('data-name', 'acaoResponsavel')
    $refs.assigneeCodUser.setAttribute('data-name', 'codacaoResponsavel')
  }

  function mountDatePicker () {
    $($refs.duedate)
      .datepicker({
        language: 'pt-BR',
        format: 'dd/mm/yyyy'
      })

    $refs.duedate.autocomplete = 'off'
  }

  function mountFormValidation () {
    $refs.btnConfirm = $refs.drawer
      .querySelector('[data-drawer-confirm]')

    $refs.fields = [
      ...$refs.drawer.querySelectorAll('[data-name]')
    ]

    $refs.btnConfirm.disabled = true

    $refs.fields
      .forEach(field => {
        field.addEventListener('keyup', onFormFilling)
        $(field).on('change', onFormFilling)
      })
  }

  function onFormFilling () {
    const isFilled = isFormFilled()

    if (isFilled) {
      $refs.btnConfirm.disabled = false
    } else {
      $refs.btnConfirm.disabled = true
    }
  }

  function isFormFilled () {
    return !$refs.fields
      .some(field => !field.value)
  }
}

export default DrawerForm()
