import ListUtil from './list-util'
import ListRemote from './list-remote'

const ActionList = function () {
  const state = {
    emitter: null
  }

  return {
    init
  }

  /**
   * üîë Public Methods
   */
  function init ({ alias, emitter }) {
    const mountComponents = [
      'criacao',
      'acoes',
      'monitoramento'
    ].includes(alias)

    state.emitter = emitter
    state.alias = alias

    state.emitter.on('list-add', ListUtil.append)
    state.emitter.on('list-clear', ListUtil.clear)

    if (mountComponents) {
      mount()
    }
  }

  /**
   * üîí Private Methods
   */
  function mount () {
    const { emitter } = state

    ListUtil.init({ emitter })

    if (
      state.alias === 'criacao' ||
      state.alias === 'acoes'
    ) {
      ListUtil.mount()
    }

    if (state.alias === 'monitoramento') {
      ListRemote.mount()
    }
  }
}

export default ActionList()
