import resolveReference from '../helpers/component-reference'

const defaults = {
  drawerOpenClass: '-open',
  fieldRef: 'name',
  titleRef: '[data-drawer-title]',
  openRef: '[data-drawer-open]',
  closeRef: '[data-drawer-close]',
  confirmRef: '[data-drawer-confirm]',
  removeRef: '[data-drawer-remove]',
  enableRemove: false,
  closeOnClickOutside: false,
  confirmText: 'Confirmar',
  bsDatepickerClass: 'datepicker'
}

export default function Drawer (reference, params) {
  params = {
    ...defaults,
    ...params,
    name: 'Drawer'
  }

  const elements = resolveReference(reference, params.name)

  if (elements instanceof HTMLElement) {
    return createInstance(elements, params)
  }

  return elements
    .map(element => createInstance(element, params))
    .filter(element => element !== undefined)
}

function createInstance (drawer, params) {
  const changeEvent = new Event('change')
  const btnOpen = document.querySelectorAll(params.openRef)
  const btnClose = drawer.querySelectorAll(params.closeRef)
  const btnConfirm = drawer.querySelector(params.confirmRef)
  const btnRemove = drawer.querySelector(params.removeRef)
  const drawerTitle = drawer.querySelector(params.titleRef)
  const pageBody = document.body

  const state = {
    isOpen: false,
    ref: null
  }

  const defaultProps = {
    title: params.title,
    confirmText: params.confirmText,
    enableRemove: params.enableRemove,
    onConfirm: params.onConfirm,
    onConclude: params.onConclude,
    onRemove: params.onRemove
  }

  const instance = {
    drawer,
    open,
    close,
    populate
  }

  addTriggers()
  mount()

  instance.drawer._drawer = instance

  return instance

  /**
   * üîí Private Methods
   */
  function mount () {
    setProps(defaultProps)
  }

  function setProps (props) {
    if (props.enableRemove) {
      btnRemove.style.display = 'initial'
    } else {
      btnRemove.style.display = 'none'
    }

    if (props.ref) {
      state.ref = props.ref
    }

    if (props.onRemove) {
      state.onRemove = props.onRemove
    }

    if (props.onConfirm) {
      state.onConfirm = props.onConfirm
    }

    if (props.onConclude) {
      state.onConclude = props.onConclude
    }

    drawerTitle.textContent = props.title
    btnConfirm.textContent = props.confirmText
  }

  function addTriggers () {
    btnOpen.forEach(btn =>
      btn.addEventListener('click', e => {
        e.stopPropagation()
        e.preventDefault()
        open(defaultProps)
      })
    )

    btnClose.forEach(btn =>
      btn.addEventListener('click', e => {
        e.preventDefault()
        close()
      })
    )

    btnConfirm
      .addEventListener('click', handleConfirm)

    btnRemove.addEventListener('click', e => {
      e.preventDefault()

      if (state.onRemove) {
        state.onRemove(state.ref)
        close()
      }
    })
  }

  function handleConfirm (e) {
    const data = getDrawerData()

    e.preventDefault()

    if (state.onConfirm) {
      const confirmRes = state.onConfirm(state.ref, data)

      addLoadingState()

      if (Array.isArray(confirmRes)) {
        Promise.all(confirmRes)
          .then(res => handleConfirmDone(res, data))
      } else {
        Promise.resolve(confirmRes)
          .then(res => handleConfirmDone(res, data))
      }
    }
  }

  function handleConfirmDone (res, data) {
    removeLoadingState()

    if (state.onConclude) {
      state.onConclude(res, data)
    }

    close()
  }

  function handleWindowOnOpen () {
    pageBody.style.overflow = 'hidden'

    if (params.closeOnClickOutside) {
      pageBody.addEventListener('click', onClickOutside, false)
    }
  }

  function handleWindowOnClose () {
    pageBody.style.overflow = 'initial'

    if (params.closeOnClickOutside) {
      pageBody.removeEventListener('click', onClickOutside, false)
    }
  }

  function onClickOutside (e) {
    const hasPicker = [...e.path]
      .some(el => el.classList
        ? el.classList.contains(params.bsDatepickerClass)
        : false
      )

    if (hasPicker) {
      return
    }

    if (!instance.drawer.contains(e.target)) {
      close()
    }
  }

  function getDrawerData () {
    return [
      ...instance.drawer.querySelectorAll(`[${params.fieldRef}]`)
    ]
      .reduce((fields, field) => {
        const id = field.getAttribute(params.fieldRef)
        return {
          ...fields,
          [id]: field.value
        }
      }, {})
  }

  function addLoadingState () {
    btnConfirm.classList.add('-loading')
  }

  function removeLoadingState () {
    btnConfirm.classList.remove('-loading')
  }

  function clearForm () {
    instance.drawer
      .querySelectorAll(`[${params.fieldRef}]`)
      .forEach(field => {
        field.value = null
        field.dispatchEvent(changeEvent)
      })
  }

  /**
   * üîë Public Methods
   */
  function open (props) {
    instance.drawer.classList.add(params.drawerOpenClass)
    state.open = true

    if (props) {
      setProps(props)
    }

    handleWindowOnOpen()
  }

  function close () {
    instance.drawer.classList.remove(params.drawerOpenClass)
    state.open = false

    clearForm()
    handleWindowOnClose()

    // @todo reset scroll to top
    // drawer-body
  }

  function populate (data) {
    Object.entries(data)
      .forEach(([id, value]) => {
        const field = instance.drawer.querySelector(`[${params.fieldRef}=${id}]`)

        if (field) {
          field.value = value
          field.dispatchEvent(changeEvent)
        }
      })
  }
}
